:root {
    /* Définitions des variables pour le thème clair */
    --color-primary: rgb(255, 255, 255);
    --color-secondary: #ffffff;
    --color-background: rgba(22, 192, 169, 0.671);
    --color-gold: #e1ab49;
    --color-green: rgb(255, 255, 255);
    --font-title: "Orbitron", sans-serif;
    --back-card: rgba(128, 128, 128, 0.589);
    --back-modal: rgba(2, 179, 155, 0.973);
    --color-active: black;
  }
  
  /* Override pour le thème sombre */
  .dark {
    --color-primary: white;
    --color-secondary: #ffffff8f;
    --color-background: rgb(1, 2, 3);
    --color-gold: #e1ab49;
    --color-green: aquamarine;
    --font-title: "Orbitron", sans-serif;
    --back-card: rgba(128, 128, 128, 0.164);
    --back-modal: rgba(41, 40, 40, 0.979);
    --color-active: aquamarine;
  }

body {
    color: var(--color-primary);
    background-color: var(--color-background);
    font-family: "Roboto Mono", monospace;
    margin:0;
}
.body-title{
    display:none;
}
.title {
    font-size:2rem;
}
.container {
    position: relative;
    width: 100%;
    min-height: 100vh; 
    display: flex;
    flex-direction: row; 
    justify-content: center;  
    overflow: hidden;  
}

.sidebar {
    width: 30%;
}
.content {
    width: 65%;
    display: flex;
    justify-content: center;
}
@media (max-width: 1500px) {
    .container {
        flex-direction: column;
        height: auto; 
    }
    .sidebar,
    .content {
        width: 100%; 
    }
}
